.page-header {
  position: relative;

  &::before {
    content: "";
    @include p-abs-center(0 auto);
    z-index: 2;
    width: 100%;
    height: 40px;
    background-image: url("../../img/overlay.png");
    @include back;
  }
}

.page-header__item:nth-child(1) {
  grid-column: span 3;
}

.page-header__item:nth-child(2) {
  grid-column: span 6;
}

.page-header__item:nth-child(3) {
  grid-column: span 3;
}

.page-header__item:nth-child(3) .phone {
  display: none;
}
.page-header--inner {
  margin-bottom: 30px;
}


.page-header--inner .page-header__item:nth-child(3) .phone {
  display: block;
  text-align: right;
}

.page-header__wrapper {
  position: relative;
  z-index: 2;
  max-width: 99%;
  margin: 0 auto;
  margin-top: 3px;
  background-color: $white;
}

.page-header__inner {
  @include d-grid(repeat(12, 1fr), center);
}

.page-header__submenu {
  background-color: $grey-light;

}


@media(max-width: 1000px) {
  .page-header__item:nth-child(1) {
    grid-column: span 12;
    text-align: right;
}

  .page-header__item:nth-child(2),
  .page-header__item:nth-child(3) {
    display: none;
  }

  .page-header__submenu {
    display: none;
  }
}
